@keyframes fadeleft {
    from {
        opacity:0;
        transform: translateX(-200px);
    }
    to {
        opacity:1;
        transform: translateX(0px);
    }
}
@keyframes faderight {
from {
    opacity:0;
    transform: translateX(200px);
}
to {
    opacity:1;
    transform: translateX(0px);
}
}
@keyframes animate {
0% {
    transform: translateY(0);
    opacity: 0;
}
10% {
    opacity: 1;
}
90% {
    opacity: 1;
}
100% {
    transform: translateY(-2000%);
    opacity: 0;
}
}